<template>
  <v-container fluid>
    <v-layout row>
      <v-flex xs6>
        <v-subheader>구하고 싶은 약물의 종류는?</v-subheader>
      </v-flex>
      <v-radio-group v-model="drugtype" xs6 row>
        <v-radio label="Tazo" value="tazo"/>
        <v-radio label="Others" value="others"/>
      </v-radio-group>
    </v-layout>

    <v-layout row>
      <v-flex xs6>
        <v-subheader>1 Vial은 몇 gram 인가요?</v-subheader>
      </v-flex>
      <v-flex xs6>
        <v-text-field label="Amount" v-model="vialunit" suffix="g"></v-text-field>
      </v-flex>
    </v-layout>

    <v-layout row>
      <v-flex xs6>
        <v-subheader>몇 cc의 식염수에 섞었나요?</v-subheader>
      </v-flex>
      <v-flex xs6>
        <v-text-field label="Amount" v-model="ccunit" suffix="cc"></v-text-field>
      </v-flex>
    </v-layout>

    <v-layout row>
      <v-flex xs6>
        <v-subheader>몇 Vial을 얻고 싶으세요?</v-subheader>
      </v-flex>
      <v-flex xs6>
        <v-text-field label="Amount" v-model="vial" suffix="vial"></v-text-field>
      </v-flex>
    </v-layout>

    <v-layout row justify-center align-center align-content-center>
      <v-subheader>환자에게&nbsp;
        <strong>{{ cc }} cc</strong>를 투약해주세요!
      </v-subheader>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: function () {
    return {
      drugtype_: 'tazo',
      vialunit: 4.5,
      ccunit: 20,
      vial: 1
    }
  },
  computed: {
    cc: function () {
      return this.ccunit * this.vial
    },
    drugtype: {
      get: function () {
        return this.drugtype_
      },
      set: function (newValue) {
        this.drugtype_ = newValue
        if (this.drugtype_ === 'tazo') {
          this.vialunit = 4.5
          this.ccunit = 20
        } else {
          this.vialunit = 1
          this.ccunit = 5
        }
      }
    }
  }
}
</script>

